
 .centerBox {
  position: relative;  
  top: 75px;
  height: 500px;                 
  width: 550px;
  margin: 0 auto 0;
  border: solid;
  border-width: thin;
  border-radius: 15px;
  background-color: #303030;
}

.head {
  background-color: #303030;
  color: #D8D8D8;
}

.info {
  border: solid;
  border-width: thin;
  border-radius: 15px;
  width: 94%;
  display: inline-block;
}

 .men {
  font-family: Menlo;
  font-size:  14px;
  color: #D8D8D8;
}

.box {
  position: relative;
  background-color: #181818;
  overflow: scroll;
  height: 200px; 
}

.nav-link, 
.nav-link:hover {
  background-color: #303030;
  color: #D8D8D8;
}

.nav-link.active {
  background-color: #303030;
  color: #D8D8D8;
  border: solid;
  border-width: thin;
  border-color: #5bc0de;
}

.linked {
  color: white;
  text-decoration: none;
}
.linked:hover {
  color: #5bc0de;
  text-decoration: none;
}
